<template>
  <div class="wiki-publish-article">
    <publicHeader></publicHeader>
    <div class="wiki-publish-article-title">
      <el-input type="text" placeholder="" v-model="text" maxlength="15" show-word-limit></el-input>
    </div>
    <div class="wiki-publish-article-block">
      <div>
        <span class="demonstration">:</span>
        <el-cascader expand-trigger="hover" :options="options" v-model="selectedOptions"></el-cascader>

        <span class="demonstration">:</span>
        <el-cascader expand-trigger="hover" :options="options1" v-model="selectPoints"></el-cascader>

        <span class="demonstration">:</span>
        <el-cascader expand-trigger="hover" :options="options2" v-model="selectLevel"></el-cascader>
      </div>
    </div>
    <div class="wiki-publish-article-md">
      <md id="contentEditor" ref="contentEditor" :zIndex="20" v-model="html"></md>
      <el-button type="primary" @click="submission"></el-button>
    </div>
  </div>
</template>

<script>
import publicHeader from "@/components/publicHeader";
import md from "@/components/publicMarkdown";
import * as ajax from "@/api";

export default {
  components: { publicHeader, md },
  data() {
    return {
      html: "",
      selectedOptions: "", //
      selectPoints: "", //
      selectLevel:'', //
      options: [],
      options1: [],
      options2:[],
      text: ""
    };
  },
  created() {},
  methods: {
    submission() {
      if (confirm("")) {
      } else {
      }
    },

    submission() {
      if (!this.text) {
        alert("");
      } else if (!this.selectedOptions) {
        alert("");
      }else if(!this.selectPoints){
        alert('')
      } else if(!this.selectLevel){
        alert('')
      }else if (!this.html) {
        alert("");
      } else {
        ajax.publishArticle(this.selectedOptions[1], this.text,this.selectLevel[0] ,this.selectPoints[0],this.html)
          .then(resp => {
            if (resp.data.code) {
              alert('')
              this.$router.push("/mine");
            } else {
              this.$message.error(resp.msg);
            }
          });
      }
    }
  },
  created() {
    ajax.getType().then(resp => {
      if (resp.data.code) {
        let list = resp.data.data.all_info;
        list.forEach(element => {
          element["value"] = element["type"]["id"];
          element["label"] = element["type"]["type_name"];
          element["children"] = element["books"];
          element["children"].forEach(item => {
            item["value"] = item["id"];
            item["label"] = item["book_name"];
          });
        });
        this.options = list; //
        let list1 = resp.data.data.all_content_types; 
        list1.forEach(item => {
          item["value"] = item["id"];
          item["label"] = item["type"];
        });
        this.options1 = list1; //
      }
    });

    ajax.getLookLevel().then(resp => {
      if(resp.data.code){
        let list = resp.data.data.limit_level
        list.forEach(item=>{
          item["value"] = item["level"];
          item["label"] = item["level_describe"];
        })
        this.options2 = list //
      }
    });
  }
};
</script>

<style lang='scss'>
.wiki-publish-article {
  min-height: 100%;
  &-text {
    width: 100%;
    height: auto;
    padding: 30px;
  }
  &-title {
    width: 100%;
    .el-input {
      width: 1280px;
      margin: 0 auto;
      margin-top: 30px;
      display: block;
    }
  }
  &-block {
    margin: 20px 0 20px 0;
    > div {
      width: 1280px;
      margin: 0 auto;
      .demonstration {
        margin-right: 20px;
      }
      .el-input--suffix {
        width: 200px;
        margin-right: 20px;
      }
    }
  }

  .wiki-publish-article-md {
    width: 100%;
    position: relative;
    .simplemde-container {
      width: 1280px;
      margin: 0 auto;
      .CodeMirror {
        min-height: 580px;
        .CodeMirror-scroll {
          min-height: 580px;
        }
      }
    }
  }

  .el-button {
    width: 100px;
    height: 30px;
    line-height: 9px;
    font-size: 12px;
    text-align: center;
  }
  .el-button--primary {
    position: absolute;
    top: 11px;
    right: 130px;
  }
}
</style>
